.partners {
  &__title {
    font-weight: 500;
    font-size: 28px;
    line-height: 160%;
    text-transform: uppercase;
    color: $special_black-blue;
    margin-bottom: 30px;
    letter-spacing: 0;

    @media (min-width: $desktop-width) {
      font-size: 36px;
    }
  }

  &__inner {
    display: flex;
    flex-direction: column;
    row-gap: 16px;
    align-items: center;

    @media (min-width: $tablet-width) {
      flex-direction: row;
      flex-wrap: wrap;
      column-gap: 20px;
    }

    @media (min-width: $desktop-width) {
      gap: 30px;
    }
  }

  &__border {
    border: 1px solid $special_light-gray;
    border-radius: 5px;
    min-width: 335px;
    padding: 24px;

    &:hover {
      cursor: pointer;
      box-shadow: -3px 0px 40px rgba(0, 0, 0, 0.1), 0px 0px 10px rgba(164, 164, 164, 0.03);
    }

    @media (min-width: $tablet-width) {
      min-width: 229px;
      padding: 14px;
    }

    @media (min-width: $desktop-width) {
      min-width: 270px;
      padding: 26px 17px;
    }
  }

  &__img {
    @media (min-width: $tablet-width) {
      width: 199px;
    }

    @media (min-width: $desktop-width) {
      width: 234px;
    }
  }
}
