.page {
  position: relative;

  &__header {
    margin-bottom: 70px;
    border-bottom: 1px solid $medium_gray;
    border-top: 1px solid #c0c8db;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.05);

    @media (min-width: $tablet-width) {
      border-bottom: none;
    }

    &::before {
      content: "";
      position: absolute;
      border-bottom: 1px solid #d9d9d9;
      left: 0;
      top: 63px;
      width: 100%;

      @media (min-width: $tablet-width) {
        content: "";
        position: absolute;
        border-bottom: none;
        left: 0;
        top: 62px;
        width: 100%;
      }
    }
  }

  &__body {
    min-width: 375px;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      min-width: 768px;
    }

    @media (min-width: $desktop-width) {
      max-width: 1440px;

    }
  }

  &__nav {
    padding: 16px 0 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    row-gap: 13px;
    position: relative;

    @media (min-width: $tablet-width) {
      flex-wrap: nowrap;
      padding: 15px 0;

      &::before {
        content: "";
        position: absolute;
        border-left: 1px solid $special_light-gray;
        height: 100%;
        right: 148px;
      }
    }

    @media (min-width: $desktop-width) {
      padding: 16px 0;

      &::before {
        right: 150px;
      }
    }

    &-logo {
      @media (min-width: $tablet-width) {
        margin-right: 105px;
      }

      @media (min-width: $desktop-width) {
        margin-right: 64px;
      }
    }

    &-svg {
      position: absolute;
      bottom: 8px;

      @media (min-width: $tablet-width) {
        bottom: 28px;
        margin-left: 9px;
      }

      @media (min-width: $desktop-width) {
        // bottom: 30px;
      }
    }

    &-label {
      order: 1;
      width: 100%;
      cursor: pointer;

      @media (min-width: $tablet-width) {
        order: 0;
        // width: auto;
        padding-left: 50px;
        padding-right: 70px;
      }

      @media (min-width: $desktop-width) {
        padding-right: 80px;
      }
    }

    &-search {
      width: 100%;
      height: 40px;
      border: none;
      outline: none;
      appearance: none;
      cursor: pointer;
      order: 1;
      padding-left: 27px;

      @media (min-width: $tablet-width) {
        border: 1px solid $special_light-gray;
        border-radius: 5px;
        order: 0;
        padding-left: 38px;
      }

      &::placeholder {
        // padding-left: 27px;
        font-family: 'Roboto', sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;

        @media (min-width: $tablet-width) {
          // padding-left: 35px;
        }
      }
    }

    &-login {
      display: flex;
      align-items: center;
      position: relative;
      padding-top: 5px;

      & picture {
        @media (min-width: $tablet-width) {
          width: 40px;
        }

        @media (min-width: $desktop-width) {
          width: 42px;
        }
      }

      @media (min-width: $desktop-width) {
        padding-top: 3px;
      }
    }

    &-text {
      display: none;

      @media (min-width: $tablet-width) {
        display: block;
        padding-right: 10px;
        color: $special_dark-grey;
      }
    }

    &-logo--login {
      cursor: pointer;

      @media (min-width: $tablet-width) {
        width: 40px;
      }
    }
  }
}
